<template>
  <div class="restaurant-menu">
    <div class="restaurant-menu-main">
      <div class="search-item" :class="{ searchItemHide: hide() }">
        <i class="fas fa-search"></i>
        <input type="search" placeholder="Tìm món ... " v-model="search" />
      </div>
      <p>MENU</p>
      <div
        class="menu-item"
        v-for="(dish, index) in this.$store.state.dishes"
        :key="index"
      >
        <div
          class="item"
          v-show="
            xoa_dau(dish.name)
              .toLowerCase()
              .indexOf(xoa_dau(search).toLowerCase()) >= 0
              ? true
              : false
          "
        >
          <img
            :src="require(`@/assets/images/${dish.image}`)"
            :alt="dish.name"
          />
          <h5>{{ dish.name }}</h5>
          <span>
            {{ dish.price.toLocaleString()}}
            <sup style="color: #3879ef">đ</sup>
            &nbsp;&nbsp;&nbsp;

            <i class="btn fas fa-plus-square fa-2x" @click="dish.val++"></i>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data(){
    return {
      search: '',
      hide(){
        return this.$store.state.checkoutShow
      },
      xoa_dau: function(str) {
          str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
          str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
          str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
          str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
          str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
          str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
          str = str.replace(/đ/g, "d");
          str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
          str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
          str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
          str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
          str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
          str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
          str = str.replace(/Đ/g, "D");
          return str;
      },
      formartNumber: function(num){
        return num.toFixed(1).replace(/\d(?=(\d{3})+\.)/g, '$&,');
      }
    }
  },
  
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.restaurant-menu .restaurant-menu-main {
  margin: 0 2% 0 0;
  background: #fff;
  border-radius: 5px;
  border: 0.5px solid #d8d8d8;
  position: relative;
}
.restaurant-menu .restaurant-menu-main .searchItemHide {
  position: static !important;
}
.restaurant-menu .restaurant-menu-main .search-item {
  border: 1px solid #d8d8d8;
  margin: 15px;
  border-radius: 5px;
  padding: 0;
  position: sticky;
  top: 85px;
  background: #fff;
  z-index: 100;
  outline: 15px solid #fff;
}
.restaurant-menu .restaurant-menu-main .search-item i {
  margin-left: 15px;
  position: absolute;
  padding: 13px 0;
}
.restaurant-menu .restaurant-menu-main .search-item input {
  border: 0px;
  font-size: 15px;
  height: 40px;
  padding: 0 10px 0 40px;
  width: 100%;
}
.restaurant-menu .restaurant-menu-main .search-item input:focus {
  outline: none;
  border: 1.5px solid rgb(0, 195, 255);
  border-radius: 5px;
}
.restaurant-menu .restaurant-menu-main p {
  font-size: 15px;
  font-weight: 500;
  color: #999999;
  margin: 27px 0px 30px 8px;
}
.restaurant-menu .restaurant-menu-main .menu-item .item {
  margin: 0 20px;
  margin-top: 10px;
  padding-bottom: 10px;
  position: relative;
  border-bottom: 1px solid #e7e5e5;
}
.restaurant-menu .restaurant-menu-main .menu-item .item img {
  display: inline;
  width: 60px;
  height: 60px;
}
.restaurant-menu .restaurant-menu-main .menu-item .item h5 {
  text-transform: capitalize;
  width: fit-content;
  display: inline-block;
  position: absolute;
  padding-left: 15px;
  padding-top: 0px;
  font-weight: 600;
  font-size: 17px;
  color: #2c2c2cdc;
}
.restaurant-menu .restaurant-menu-main .menu-item .item span {
  right: 0;
  bottom: 15px;
  position: absolute;
  font-size: 18px;
  font-weight: 700;
  color: #3879ef;
}
.restaurant-menu .restaurant-menu-main .menu-item .item span .fa-plus-square {
  font-size: 23px;
  padding-top: 5px;
  color: #b61111c7;
}
.restaurant-menu
  .restaurant-menu-main
  .menu-item
  .item
  span
  .fa-plus-square:hover {
  color: #9c0000;
}
</style>
