<template>
  <div style="posision: fixed; top:0px">
    <div class="list-choosed">
      <div class="order-card-group">
        <div class="order-card-user">
          <img
            alt="Nguyên Phạm"
            src="https://images.foody.vn/usr/g2344/23434272/avt/s60x60/foody-avatar-c9d33565-bf49-44bb--847c7688-210320213918.jpeg"
          />
          <span>Nguyên Phạm</span>
          <span class="so-mon">{{ totalB() }} món</span>
        </div>
        <div class="order-card-groups">
          <div v-for="(dish, index) in this.$store.state.dishes" :key="index">
            <div class="order-card-item" v-if="dish.val > 0 ? true : false">
              <div class="clearfix">
                <button
                  class="fas fa-plus-square add"
                  @click="(dish.val += 1) && totalA()"
                ></button>
                <span class="number-order">{{ dish.val }}</span>
                <button
                  class="fas fa-minus-square remove"
                  @click="(dish.val -= 1) && totalA()"
                ></button>
                <span class="name-order"> {{ dish.name }}</span>
              </div>
              <div class="note-order">
                <input
                  type="text"
                  id="txtNote"
                  placeholder="Thêm ghi chú..."
                  value=""
                />
                <span class="price-order"
                  >{{ (dish.total = dish.price * dish.val).toLocaleString() }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row-bill-restaurant relative">
        <div class="total row">
          <div class="col">Tổng cộng</div>
          <div class="col-auto">
            &nbsp;
            <span class="txt-bold txt-blue font16">
              {{ totalA().toLocaleString() }}
              <span
                style="
                  font-weight: 400;
                  position: relative;
                  top: -9px;
                  font-size: 10px;
                  right: 0;
                "
                >đ
              </span>
            </span>
          </div>
        </div>
      </div>
      <button
        class="btn btn-danger"
        @click="show()"
        href="#class-box"
      >
        <i class="fas fa-check-circle" style="color: aliceblue"></i>
        Đặt trước
      </button>
    </div>
  </div>
</template>
<script>
import "./checkOut.vue"
export default {
  data() {
    return {
      num: 0,
      hahha: 0,
      show(){
        this.$store.state.checkoutShow= !this.$store.state.checkoutShow
      }
    };
  },

  methods: {
    totalA: function () {
      this.hahha = 0;
      this.$store.state.dishes.forEach((dish) => {
        this.hahha += dish.price * dish.val;
      });

      return this.hahha;
    },
    totalB: function () {
      this.num = 0;
      this.$store.state.dishes.forEach((dish) => {
        if (dish.val > 0) this.num += 1;
      });
      //console.log(this.$store.status.checkoutShow);
      return this.num;
    },
    // on('click', "i.close, #over", function() {
    //             '#over, .class'.fadeOut(300 , function() {
    //                 '#over'.remove();
    //                 '.checkout-form'.removeAttr('style');
    //             });
    //             return false;
    //         });
  },
  
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
.list-choosed {
  width: 310px;
  border: 1px solid #64c4ed;
  border-radius: 5px;
  background: #fff;
}
.list-choosed .order-card-group .order-card-user {
  background: #64c4ed;
  line-height: 60px;
  padding-left: 10px;
  border-radius: 5px 5px 0 0;
}
.list-choosed .order-card-group .order-card-user img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}
.list-choosed .order-card-group .order-card-user span {
  color: #9c0000b4;
  font-size: 14px;
  font-weight: 700;
  margin-left: 15px;
}
.list-choosed .order-card-group .order-card-user span.so-mon {
  font-size: 14px;
  font-weight: 400;
  color: #2e2e2e;
  margin-left: 100px;
}
.list-choosed .order-card-group .order-card-groups button {
  border: 0px;
}
.list-choosed .order-card-group .order-card-groups .fa-plus-square {
  color: #3879ef;
}
.list-choosed .order-card-group .order-card-groups .fa-minus-square {
  color: #302d2dc5;
}
.list-choosed
  .order-card-group
  .order-card-groups
  .order-card-item
  .clearfix
  .number-order {
  color: #9c0000;
  font-size: 14px;
  font-weight: bold;
  margin: 0 3px;
}
.list-choosed
  .order-card-group
  .order-card-groups
  .order-card-item
  .clearfix
  .name-order {
  font-size: 14px;
  font-weight: 550;
  padding-left: 5px;
  text-transform: capitalize;
}
.list-choosed
  .order-card-group
  .order-card-groups
  .order-card-item
  .note-order
  input {
  border: 0;
  font-size: 13px;
}
.list-choosed
  .order-card-group
  .order-card-groups
  .order-card-item
  .note-order
  input:focus {
  outline: 0;
}
.list-choosed
  .order-card-group
  .order-card-groups
  .order-card-item
  .note-order
  .price-order {
  font-size: 13px;
  color: #161212;
  float: right;
}
.list-choosed .order-card-group .order-card-groups .order-card-item {
  border-bottom: 0.5px solid #bebcbc;
  padding: 8px 10px 0 10px;
}
.list-choosed .row-bill-restaurant .total {
  height: 50px;
  background: #f0f0f0;
  line-height: 50px;
  margin: 0 0px;
  border-bottom: 1px solid #999999;
}
.list-choosed .row-bill-restaurant .total .col {
  font-size: 13px;
  color: #1a0005;
}
.list-choosed .row-bill-restaurant .total .col-auto span {
  font-size: 15px;
  color: #3878efde;
  font-weight: 650;
}
.list-choosed .btn-danger {
  margin: 15px 10px;
  width: 94%;
}
</style>