<template>
  <div>
    <transition
      name="custom-classes-transition"
      leave-active-class="animate__animated animate__zoomOutDown"
      enter-active-class="animate__animated animate__zoomInDown"
      style="--animate-delay: 1s"
    >
      <div class="checkout-form" v-show="this.$store.state.checkoutShow && totalA()>0">
        <div class="checkout" id="class-box">
          <div class="checkout-title">
            <label>TIẾN HÀNH ĐẶT HÀNG</label>
            <i class="far fa-times-circle close" @click="hide()"></i>
          </div>
          <form class="checkout-content" action="#" method="">
            <label class="ship-from">
              <b>Giao từ: </b>
              <input type="text" value="hahhahahahahahahhaah" disabled />
              <p>kkkkkk</p>
            </label>
            <label class="content-title">
              <div><i class="fas fa-map-marker-alt"></i></div>
              <input
                class="content-title-val"
                type="text"
                autocomplete="on"
                required
                name="address"
                placeholder="Nhập địa chỉ chính xác"
                value=""
              />
            </label>
            <label class="content-title" style="width: 50%; float: left">
              <div><i class="fas fa-user-circle"></i></div>
              <input
                class="content-title-val"
                type="text"
                autocomplete="on"
                required
                name="name"
                placeholder="Nhập tên người nhận"
                value=""
              />
            </label>
            <label class="content-title" style="width: 50%; float: left">
              <div><i class="fas fa-phone-alt"></i></div>
              <input
                class="content-title-val"
                type="text"
                autocomplete="on"
                required
                name="phone"
                placeholder="Nhập số điện thoại"
                value=""
              />
            </label>
            <div style="clear: both"></div>
            <div class="content-cart">
              <div class="cartinfo">Chi tiết đơn hàng</div>
              <div v-for="(dish, index) in dishes" :key="index">
                <div class="cart-item-checkout" v-if="dish.val>0 ? true : false">
                  <div>{{ dish.val }}</div>
                  &nbsp;
                  <b>{{ dish.name }}</b>
                  <span>
                    {{dish.total.toLocaleString()}}
                    <span
                      style="
                        font-weight: 400;
                        position: relative;
                        top: -7px;
                        font-size: 10px;
                        right: 0px;
                        text-transform: lowercase;
                      "
                      >đ
                    </span>
                  </span>
                </div>
              </div>
              <div class="total-cart">
                <p>
                  Tổng giá <!--<b>{{ totalB() }}</b> phần -->
                  <span style="float: right"
                    ><b>{{totalA().toLocaleString()}}</b>

                    <span
                      style="
                        font-weight: 400;
                        position: relative;
                        top: -8px;
                        font-size: 10px;
                        right: 0px;
                      "
                      >đ
                    </span>
                  </span>
                </p>
                <p>
                  Phí dịch vụ:
                  <span>
                    1.000
                    <span
                      style="
                        font-weight: 400;
                        position: relative;
                        top: -8px;
                        font-size: 10px;
                        right: 0px;
                      "
                      >đ
                    </span>
                  </span>
                </p>
                <p>
                  Phí vận chuyển:
                  <span>
                    20.000
                    <span
                      style="
                        font-weight: 400;
                        position: relative;
                        top: -8px;
                        font-size: 10px;
                        right: 0px;
                      "
                      >đ
                    </span>
                  </span>
                </p>
              </div>
              <div class="total">
                Tổng cộng:
                <span>
                  {{(totalA() + 21000).toLocaleString()}}
                  <span
                    style="
                      font-weight: 400;
                      position: relative;
                      top: -8px;
                      font-size: 12px;
                      right: -2px;
                    "
                    >đ
                  </span>
                </span>
              </div>
            </div>

            <button class="success" type="submit" value="Đặt hàng">Đặt hàng </button>
          </form>
        </div>
      </div>
      
    </transition>
    <div class="noti">
        <h2  v-if="totalA()>0?false:true" v-show="this.$store.state.checkoutShow">Không có sản phẩm trong giỏ hàng!</h2>
      </div>
  </div>
</template>
<script>
import "./cart.vue";
export default {
  data() {
    return {
      hide() {
        this.$store.state.checkoutShow = !this.$store.state.checkoutShow;
      },
      dishes: this.$store.state.dishes,
      num:0,
     
    };
  },
  methods:{
     totalB: function () {
      this.num = 0;
      this.$store.state.dishes.forEach((dish) => {
        this.num += dish.val;
      });
      //console.log(this.$store.status.checkoutShow);
      return this.num;
    },
    totalA: function () {
      this.hahha = 0;
      this.$store.state.dishes.forEach((dish) => {
        this.hahha += dish.price * dish.val;
      });

      return this.hahha;
    },
    
  }
};
</script>
